.derecha {
    display: flex;
    flex-direction: column;
    align-items: center;

    @include tablet {
        align-items: flex-end;
    }
    .dark-mode-boton {
        width: 3rem;
        margin: 3rem 0 2rem 0;
        filter: invert(100%);

        &:hover {
            cursor:pointer;
        }

        @include tablet {
            margin-top: 0;
        }
    }
}
.barra {
    display: flex;
    flex-direction: column;
    align-items: center;
  
    padding-top: 3rem;
    @include tablet {
        flex-direction: row;
        justify-content: space-between;
        align-items: flex-end;
    }
}

.navegacion {
    visibility: hidden;
    opacity: 0;
    height: 0;
    transition: visibility .3s ease-in-out, opacity .3s linear, height .5s linear;
    @include tablet {
        display: flex;
        align-items: center;
        opacity: 1;
        visibility: visible;
    }

a{
    color: $blanco;
    text-decoration: none;
    font-size: 2.2rem;
    display: block;
    text-align: center;

    &:hover {
        color: $verde;
    }

    @include tablet {
        margin-right: 2rem;
        font-size: 1.8rem;

        &:last-of-type {
            margin-right: 0;
        }
    }
  
}

}

// Menu de hamburguesa

.mobile-menu {
    @include tablet {
        display: none;
    }
    img {
        width: 5rem;
    }
}
.mostrar {
  visibility: visible;
  opacity: 1;
  height: auto;
  
}